@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #333;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
}

.header {
    background-color: #222;
    padding: 25px 100px;
    font-weight: bold;
}

main {
    margin: 50px 100px;
    width: 50%;
    float: left;
}

aside {
    width: 20%;
    margin-top: 50px;
    margin-left: 5%;
    float: left;
}

main .user {
    margin-bottom: 50px;
    background-color: #a5112e;
    padding: 20px;
    border-radius: 50px;
}

main .userInfo {
    display: flex;
}

main .userText {
    margin-left: 10%;
}

main .delete-icon {
    float: right;
    color: #134b57;
    cursor: pointer;
    transform: scale(2);
}

main .edit-icon {
    float: right;
    margin-right: 25px;
    color: #165664;
    cursor: pointer;
    transform: scale(2);
}

main .delete-icon:hover,
main .edit-icon:hover {
    opacity: 0.5;
}

form input:not([type='checkbox']),
form textarea,
form button {
    width: 90%;
    padding: 15px 5%;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    margin-bottom: 10px;
    border: none;
    background-color: #222;
    color: #fff;
    outline: none;
}

form input[type='checkbox'] {
    margin-bottom: 10px;
}

form button {
    width: 100%;
    cursor: pointer;
    transition: transform 500ms ease;
}

form button:hover {
    transform: scale(1.1);
}

.user form {
    margin-top: 20px;
}

.user form input,
.user form textarea,
.user form button {
    background-color: #72061b;
}

.user form button:hover {
    transform: scale(1.03);
}